.header {
    width: 100%;
    padding-top: 40px;

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo {
        height: 90px; 
        img{
            max-height: 100%;
        }
    }

    &__menu-list {
        display: flex;
        list-style: none;
    }

    &__menu-list-item {
        margin-right: 20px;
    }
}